<template>
<transition name="slide-fade">
	<div class="cc-main">
		<div class="cc-title"></div>
		<div class="cc-des"></div>
		<div class="cc-list">
			<div class="cc-input0">
				<div class="cc-tit"></div>
				<van-field class="cc-input" v-model="title" maxlength="12" placeholder="" />
			</div>
			<div class="cc-input0">
				<div class="cc-tit"></div>
				<van-field class="cc-input" v-model="type" readonly right-icon="arrow" placeholder="" @click="show = true" />
			</div>
			<div class="cc-input0">
				<div class="cc-tit"></div>
				<van-field class="cc-input" v-model="number" placeholder="" />
			</div>
			<div class="cc-input0 flexstart">
				<div class="cc-input2">
					<div class="cc-tit"></div>
					<van-field class="cc-input" input-align="center" v-model="experience" readonly placeholder="" @click="show1 = true" />
				</div>
				<div class="cc-input2 cc-r">
					<div class="cc-tit"></div>
					<van-field class="cc-input" input-align="center" v-model="education" readonly placeholder="" @click="show2 = true" />
				</div>
				<div class="cc-input2 cc-r">
					<div class="cc-tit"></div>
					<van-field class="cc-input" input-align="center" v-model="salary" readonly placeholder="" @click="show3 = true" />
				</div>
			</div>
			<div class="cc-input0">
				<div class="cc-tit"></div>
				<van-field class="cc-input" v-model="welfare" readonly right-icon="arrow" placeholder="" @click="show4 = true" />
			</div>
			<div class="cc-input0">
				<div class="cc-tit"></div>
				<van-field class="cc-input" v-model="introduce"  type="textarea" placeholder="" />
			</div>
			<div class="cc-input0">
				<div class="cc-tit"></div>
				<van-field class="cc-input" v-model="place" readonly right-icon="arrow" placeholder="" @click="show5 = true" />
			</div>
			<div class="cc-input0">
				<div class="cc-tit"></div>
				<van-field class="cc-input" v-model="address" placeholder="" />
			</div>
			<div class="cc-button" @click="fabu"></div>
		</div>
		<van-popup v-model="show" position="bottom"><van-picker show-toolbar :columns="columns" @cancel="show = false" @confirm="onConfirm" /></van-popup>
		<van-popup v-model="show1" position="bottom"><van-picker show-toolbar :columns="columns1" @cancel="show1 = false" @confirm="onConfirm1" /></van-popup>
		<van-popup v-model="show2" position="bottom"><van-picker show-toolbar :columns="columns2" @cancel="show2 = false" @confirm="onConfirm2" /></van-popup>
		<van-popup v-model="show3" position="bottom"><van-picker show-toolbar :columns="columns3" @cancel="show3 = false" @confirm="onConfirm3" /></van-popup>
		<van-popup v-model="show4" position="bottom">
			<van-checkbox-group max="5" v-model="welfare1" @change="changecheckbox">
				<div class="cc-boxtit1"></div>
				<div class="cc-boxtit2">5</div>
				<div class="cc-box1 flexstart">
					<div class="cc-box2" :class="{active:welfare1.indexOf(item.id)>-1}" v-for="item in columns4" v-bind:key="item.id">
						<div>{{item.name}}</div>
						<img v-show="welfare1.indexOf(item.id)>-1" class="cc-checked" src="../assets/img/checked.png" alt="">
						<van-checkbox class="cc-check" :name="item.id">{{item.name}}</van-checkbox>
					</div>
				</div>
			</van-checkbox-group>
		</van-popup>
		<van-popup v-model="show5" position="bottom"><van-area :area-list="areaList" :value="districtCode" visible-item-count="9" @confirm="confirmarea" @cancel="show5 = false" /></van-popup>
		<van-popup v-model="show6">
			<div class="cc-renli">
				<div class="cc-renli1">
					<div></div>
					<p></p>
				</div>
				<div class="cc-renlilist">
					<div class="cc-list1 spacebetween" @click="isindex=0">
						<span class="cc-d"></span>
						<div class="cc-name van-ellipsis"></div>
						<div class="cc-check1">
							<van-icon v-show="isindex!==0" color="#cccccc" size="20" name="circle" />
							<van-icon v-show="isindex===0" color="#229CAD" size="20" name="checked" />
						</div>
					</div>					
					<div class="cc-list1 spacebetween" v-for="item in columns6" v-bind:key="item.id" @click="isindex=item.adminId">
						<span class="cc-d"></span>
						<div class="cc-name van-ellipsis">{{item.companyName}}</div>
						<div class="cc-check1">
							<van-icon v-show="isindex!==item.adminId" color="#cccccc" size="20" name="circle" />
							<van-icon v-show="isindex===item.adminId" color="#229CAD" size="20" name="checked" />
						</div>
					</div>
				</div>
				<div class="cc-sub" @click="sub"></div>
			</div>
		</van-popup>
	</div>
</transition>
</template>
<script type="text/javascript">
import {Checkbox,CheckboxGroup,Field,Toast,Popup,Area,Picker ,Icon,Dialog } from 'vant';
import areaList from '@/assets/js/area.js'//
export default {
	name: 'publish',
	components: {
		[Field.name]: Field,
		[Popup.name]: Popup,
		[Toast.name]: Toast,
		[Area.name]: Area,
		[Picker.name]: Picker,
		[Checkbox.name]: Checkbox,
		[CheckboxGroup.name]: CheckboxGroup,
		[Icon.name]: Icon,
		[Dialog.name]: Dialog,
	},
	data() {
		return {
			areaList,
			show5:false,
			place:"",
			districtCode:'330203',
			province:"",
			city:"",
			district:"",
			address:"",

			title:"",
			number:"",
			introduce: "",

			show:false,
			columns: [],
			columns0: [],
			type:"",
			type1:"",

			show1:false,
			columns1: [],
			columns01: [],
			experience:"",
			experience1:"",

			show2: false,
			columns2: [],
			columns02: [],
			education:"",
			education1:"",

			show3: false,
			columns3: [],
			columns03: [],
			salary:"",
			salary1:"",

			show4: false,
			columns4: [],
			welfare:"",
			welfare1:[],

			show6: false,
			columns6: [],
			isindex:0
		};
	},
	mounted() {
		this.jobCat();
		this.screenExperience();
		this.screenEducation();
		this.jobSalary();
		this.screenWelfare();
		this.enterpriseList();
	},
	methods: {
		enterpriseList(){
			var that=this;
			this.$http.get('/company/enterpriseList').then(function (res) {
				if (res.code==10000) {
					that.columns6=res.data;
				}else{
					Toast.fail(res.msg);
				}
			}).catch(function () {
			});
		},
		//
		jobCat(){
			var that=this;
			this.$http.get('/main/jobCat').then(function (res) {
				if (res.code==10000) {
					let arr=[];
					res.data.forEach(function(element) {
						arr.push(element.name)
					});
					that.columns=arr;
					that.columns0=res.data;
				}else{
					Toast.fail(res.msg);
				}
			}).catch(function () {
			});
		},
		//
		screenExperience(){
			var that=this;
			this.$http.get('/company/screenExperience').then(function (res) {
				if (res.code==10000) {
					let arr=[];
					let arr1=res.data.slice(1)
					arr1.forEach(function(element) {
						arr.push(element.name)
					});
					that.columns1=arr;
					that.columns01=arr1;
				}else{
					Toast.fail(res.msg);
				}
			}).catch(function () {
			});
		},
		//
		screenEducation(){
			var that=this;
			this.$http.get('/company/screenEducation').then(function (res) {
				if (res.code==10000) {
					let arr=[];
					let arr1=res.data.slice(1)
					arr1.forEach(function(element) {
						arr.push(element.name)
					});
					that.columns2=arr;
					that.columns02=arr1;
				}else{
					Toast.fail(res.msg);
				}
			}).catch(function () {
			});
		},
		//
		jobSalary(){
			var that=this;
			this.$http.get('/main/jobSalary').then(function (res) {
				if (res.code==10000) {
					let arr=[];
					res.data.forEach(function(element) {
						arr.push(element.name)
					});
					that.columns3=arr;
					that.columns03=res.data;
				}else{
					Toast.fail(res.msg);
				}
			}).catch(function () {
			});
		},
		//
		screenWelfare(){
			var that=this;
			this.$http.get('/company/screenWelfare').then(function (res) {
				if (res.code==10000) {
					that.columns4=res.data.slice(1);
				}else{
					Toast.fail(res.msg);
				}
			}).catch(function () {
			});
		},
		onConfirm(value,index) {
			this.show = false;
			this.type=value;
			this.type1=this.columns0[index].id;
		},
		onConfirm1(value,index) {
			this.show1 = false;
			this.experience=value;
			this.experience1=this.columns01[index].level;
		},
		onConfirm2(value,index) {
			this.show2 = false;
			this.education=value;
			this.education1=this.columns02[index].level;
		},
		onConfirm3(value,index) {
			this.show3 = false;
			this.salary=value;
			this.salary1=this.columns03[index].id;
		},
		changecheckbox(a){
			let arr=[];
			var that=this;
			a.forEach(function(b) {
				that.columns4.forEach(function(c) {
					if (c.id==b) {
						arr.push(c.name)
					}
				});
			});
			this.welfare=arr.join("")
		},
		//
		confirmarea(res){
			this.place=res[0].name+'/'+res[1].name+'/'+res[2].name
			this.province=res[0].name;
			this.city=res[1].name;
			this.district=res[2].name;
			this.districtCode=res[2].code;
			this.show5 = false;
		},
		fabu(){
			if (this.title=="") {
				Toast.fail("");
				return false;
			}
			if (this.type1=="") {
				Toast.fail("");
				return false;
			}
			if (this.number=="") {
				Toast.fail("");
				return false;
			}
			if (this.experience1=="") {
				Toast.fail("");
				return false;
			}
			if (this.education1=="") {
				Toast.fail("");
				return false;
			}
			if (this.salary1=="") {
				Toast.fail("");
				return false;
			}
			if (this.welfare1=="") {
				Toast.fail("");
				return false;
			}
			if (this.place==""||this.districtCode=="") {
				Toast.fail("");
				return false;
			}
			if (this.address=="") {
				Toast.fail("");
				return false;
			}
			this.show6=true;
		},
		sub(){
			var that=this;
			var welfare1=this.welfare1.toString();
			this.$http.post(`/company/commitJob?adminId=${that.isindex}`,{
				name:that.title,
				city:that.place,
				code:that.districtCode,
				address:that.address,
                cate:that.type1,
                num:that.number,
                experience:that.experience1,
                education:that.education1,
                pay:that.salary1,
                welfare:welfare1,
                explain:that.introduce
			}).then(function (res) {
				if (res.code==10000) {
					that.show6=false;
					Dialog.alert({
						title: '',
						message: '',
						confirmButtonText:''
					}).then(() => {
						that.$router.back();
					});
				}else{
					Toast.fail(res.msg);
				}
			}).catch(function () {
			});
		}

	}
}
</script>
<style lang="scss" scoped>
.cc-main{
	padding:30px 24px;
	background:#fff;
}
.cc-title{
	font-size:24px;
	font-weight:600;
	color:rgba(19,37,55,1);
	line-height:33px;
	margin-bottom: 10px;
}
.cc-des{
	color:rgba(88,97,119,1);
	line-height:20px;
}
.cc-list{
	padding:9px 0 31px;
}
.cc-input0{
	padding:21px 0 20px;
	border-bottom: 1px solid #eee;
	position: relative;
	.cc-tit{
		font-weight:300;
		color:rgba(88,97,119,1);
		line-height:20px;
		margin-bottom: 8px;
	}
	.cc-input{
		width: 100%;
		font-size:18px;
		font-weight:500;
		color:rgba(64,70,91,1);
		line-height:25px;
		padding:0;
	}
	.cc-input2{
		width: 108px;
		text-align: center;
		&.cc-r{
			border-left: 1px solid #eee;
		}
	}
}
.cc-boxtit1{
	padding:24px 16px 5px;
	font-size:17px;
	font-weight:500;
	color:rgba(0,0,0,0.87);
	line-height:24px;
}
.cc-boxtit2{
	padding:0 16px 16px;
	font-size:12px;
	color:rgba(0,0,0,0.54);
	line-height:17px;
}
.cc-box1{
	padding:0 0 16px 16px;
	flex-wrap:wrap;
	.cc-box2{
		width:106px;
		height:36px;
		line-height: 36px;
		text-align: center;
		background:rgba(51,51,51,0.05);
		border: 1px solid rgba(51,51,51,0.05);
		border-radius:2px;
		margin:0 13px 14px 0;
		color:rgba(0,0,0,1);
		position: relative;
		&.active{
			border: 1px solid #219BAC;
			background:#DEFBFF;
			color:#1F98A9;
		}
		.cc-checked{
			position: absolute;
			right: 0;
			bottom: 0;
			width: 20px;
			height: 17px;
		}
		.cc-check{
			position: absolute;
			left: 0;
			top:0;
			width:106px;
			height: 36px;
			opacity: 0;
		}
	}
}
.cc-renli{
	width:270px;
	height:386px;
	background:rgba(255,255,255,1);
	border-radius:2px;
	.cc-renli1{
		padding:20px 33px 16px;
		text-align: center;
		border-bottom: 1px solid #eee;
		div{
			font-size:18px;
			font-weight:500;
			color:rgba(64,70,91,1);
			line-height:21px;
			margin-bottom: 11px;
		}
		p{
			font-size:12px;
			font-weight:300;
			color:rgba(88,97,119,1);
			line-height:17px;
		}
	}
	.cc-renlilist{
		height: 220px;
		padding:16px 24px;
		overflow-y: scroll;
		.cc-list1{
			width: 222px;
			padding:7px 0;
			.cc-d{
				width:5px;
				height:5px;
				background:rgba(7,179,203,1);
				border-radius:50%;
				margin-top: 6px;
			}
			.cc-name{
				width:185px;
				font-weight:500;
				color:rgba(64,70,91,1);
				line-height:20px;
			}
			.cc-check1{
				width: 20px;
				height: 20px;

			}
		}
	}
	.cc-sub{
		width:210px;
		height:34px;
		background:linear-gradient(314deg,rgba(30,151,168,1) 0%,rgba(52,174,191,1) 100%);
		border-radius:2px;
		font-size:18px;
		color:rgba(255,255,255,1);
		line-height:34px;
		text-align: center;
		margin:14px auto 0;
	}
}
.cc-button{
	width:327px;
	height:44px;
	background:linear-gradient(314deg,rgba(30,151,168,1) 0%,rgba(52,174,191,1) 100%);
	border-radius:2px;
	font-size:18px;
	color:rgba(255,255,255,1);
	line-height:44px;
	text-align: center;
}
</style>